.pill{padding:10px 30px 10px 10px;background-color:var(--teal-20);border:1px solid var(--teal-40);margin-right:4px;border-radius:.28571429em;font-weight:500;color:var(--black);font-size:14px}.pill:hover{background-color:var(--teal-30);border-color:var(--teal-50);cursor:pointer}.pill button,.pill .cf-icon-svg{padding:0;padding-left:5px;border:0;margin:0;background-color:rgba(0,0,0,0);color:var(--teal-80);position:absolute;right:15px;top:50%;transform:translateY(-50%)}