.map-toolbar{border-top:2px solid var(--gray-20);border-bottom:1px solid var(--gray-20);margin:0 20px}.map-toolbar a{text-decoration:underline}.map-toolbar a:link,.map-toolbar a:hover,.map-toolbar a:active,.map-toolbar a:visited{cursor:pointer}.map-toolbar .state-heading{padding:.625rem;border-bottom:1px solid var(--gray-20)}.map-toolbar .state-heading span{margin:.625rem}.map-toolbar .state-heading svg{padding-right:.313rem}.map-toolbar .state-navigation{padding:.625rem}